<template>
	<view class='container padding'>
		<view class="title">请选择与您的问题关联的订单</view>
		<view class="item flex-row-between">
			<text class="car_type text-ellipsis">EX5 进取悦风版(白色)</text>
			<text class="shop text-ellipsis">北汽智慧4S店...</text>
			<view class="icon_w flex-row-center"><image src="../../static/down_arrow.png" class="icon roate"></image></view>
		</view>
		
		<view class="title">请选择您的问题类型</view>
		<view class="item flex-row-between">
			<!-- <picker
				@change="carChange($event,item)" 
				:value="item.carIdx"
				:range="carList" 
				class="picker"
				:disabled="item.orderId"
				range-key="showName"
			>
				<view class="picker">
					<view v-if="item.orderId">{{item.carTypeName}}</view>
					<view v-else :class="[!item.carIdx?'placeholder_style':'']">
						{{item.carIdx!=null? carList[item.carIdx].name : '请选择意向车型'}}
					</view>
				</view>
			</picker> -->
			
			<text class="question_type text-ellipsis">EX5 进取悦风版(白色)</text>
			<view class="icon_w flex-row-center"><image src="../../static/down_arrow.png" class="icon"></image></view>
		</view>
		
		
		<view class="title">请输入您的投诉/建议</view>
		<view class="item flex-row-between">
			<textarea class="textarea" placeholder="填写不少于10个字的描述" maxlength="1000" auto-height v-model="text"></textarea>
			<text class="max">{{text.length}}/1000</text>
		</view>
		
		<view class="title flex-row-start-end">上传凭证<text class="little">（违规违纪类必须提供凭证）</text></view>
		<view class="upload_w">
			<view class="upload">
				<view class="item" v-for='(litt,idx) in affixList' :key='idx'>
					<img src="../../static/delete.png" class='delete' @click='deleteFile(litt)' v-if="currentStep==item.step">
					<view @click='lookAffix(litt)' class='inner_w'>
						<image :src="litt.openUrl" class='pic'/>
					</view>
				</view>
				<view class="inner item" @click='takePicture'>
					<image src="../../static/pic.png"></image>
				</view>
			</view>
		</view>
		<view class='btn_wrapper'>
			<view class="sure">
				<button class="cu-btn block lg" @click="createOrder" :disabled="btnDisabled">
					提交
				</button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				text:''
			};
		},
		methods:{
			takePicture(){
				var _that=this
				uni.chooseImage({
					count:1,
					sizeType:["compressed"],
					sourceType: ['album', 'camera'], //从相册选择
					success: function (res) {
						let imgPaths=res.tempFilePaths
						for(let i=0;i<imgPaths.length;i++){
							(function(i){
								uni.showLoading()
								uni.uploadFile({
									url:getApp().globalData.requestUrl+"/common/upload",
									filePath:imgPaths[i],
									name: 'upFile',
									header: {
										'content-type': 'multipart/form-data',
										'service-code':'QDMS',
										"ticket":JSON.parse(uni.getStorageSync('token')).value
									},
									success:(res)=>{
										const _data=JSON.parse(res.data)
										let result=_data.data
										_that.affixList.push({
											openUrl:http2Https(result.openUrl),
											fileId:result.fileId
										})
										_that.affixBtnDisabled=false
									},
									complete:(res)=>{
										uni.hideLoading()
									},
								})
							})(i)
						}
					}
				});
			},
			lookAffix(item){
				if(this.mobileModel=="VOG-AL00"){ //华为p30
					this.currentImgUrl=item.openUrl;
					this.lookImg=true
				}else{
					//获取 url数组
					var _urlList=[]
					this.affixList.forEach((item)=>{
						_urlList.push(item.openUrl)
					})
					uni.showLoading()
					uni.previewImage({
						current:item.openUrl,
						urls: _urlList,
						longPressActions:{
							itemList: ['发送给朋友', '保存图片', '收藏']
						},
						fail(err){
							this.currentImgUrl=item.openUrl;
							this.lookImg=true
						},
						complete(){
							uni.hideLoading()
						}
					})
				}
			},
			carChange(e,item){
				var _idx=e.detail.value //选的第几个
				item.carIdx=_idx
				item.carTypeId=this.carList[_idx].id
				item.carTypeName=this.carList[_idx].name
			}
		}
	}
</script>

<style lang="stylus">
	.container
		padding-bottom:20upx
		.title
			height:44upx;
			font-size:32upx;
			font-weight:600;
			color:rgba(30,30,30,1);
			margin-bottom:45upx
			&:not(:first-child)
				margin-top:60upx
			.little
				color:#8491A8
				font-size:24upx
				font-weight:normal
		.item
			width:690upx;
			min-height:100upx;
			background:rgba(248,249,252,1);
			border-radius:8upx;
			padding:0upx 30upx
			position:relative
			.car_type
				width:278upx
				margin-right:40upx
			.shop
				width 198upx
				margin-right:40upx
			.question_type
				margin-right:40upx
			.icon_w
				padding:20upx 0upx 20upx 20upx
				.icon
					width:28upx
					height:28upx
					&.roate
						transform rotateZ(-90deg)
			.textarea
				padding:30upx 0upx
				width:100%
				font-size:28upx
				min-height:200upx
			.max
				position:absolute
				bottom:10upx
				right:10upx
				color:rgba(178,182,192,1)
				font-size:24upx
		.upload_w
			.upload
				display:flex
				color:#1E1E1E
				flex-direction:row
				justify-content flex-start
				flex-wrap:wrap
				.item
					width:130upx
					height:130upx;
					background:rgba(249,251,252,1);
					border:2upx solid rgba(227,233,240,1);
					margin-bottom:25upx
					display:flex
					flex-direction:row
					align-items:center
					justify-content center
					position:relative
					.delete
						position:absolute
						width:45upx
						height:45upx
						right:-24upx
						top:-24upx
						z-index:2
					.inner_w
						width:100%
						height:100%
						display:flex
						flex-direction:row
						align-items:center
						justify-content center
						.img
							width:48upx
							height:48upx
						.pic
							width:100%
							height:100%
					&:not(:last-child)
						margin-right:4%
					&.inner
						display:flex
						flex-direction row
						align-items center
						image
							width:48upx
							height:48upx
		.btn_wrapper
			background:#FFFFFF
			height:155upx
			margin-top:60upx
			.lg
				height:112upx
			.sure
				button
					border-radius:16upx;
					color:#fff
					background:rgba(31,35,52,1);
</style>
